}

p {
    color: rgb(0, 0, 0);
    margin-left: 5px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: large;
}

.panel {
    background-color: rgb(180, 157, 157);
    border-radius: 10px;
    position: absolute;
    width: 192px;
    padding: 20px;
}


