.body {
    margin: 10px;
}

.title {
    color: rgb(58, 42, 42);
    font-family: lobster;
    font-size: 40px;
    font-weight: bold;
    letter-spacing: -1px;
    line-height: 1;
    animation: glow 2s ease-in-out infinite alternate;
    
}

@keyframes glow {
    from {
        text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #fff, 0 0 40px #fff, 0 0 50px #fff, 0 0 60px #fff, 0 0 70px #fff;
    }
    to {
        text-shadow: 0 0 30px #74525b, 0 0 40px #74525b, 0 0 50px #74525b, 0 0 60px #74525b, 0 0 70px #74525b, 0 0 80px #74525b, 0 0 90px #74525b;
    }
}

.data {
    font-weight: 600;
}

.cpanel tr {
    opacity: 1;
    transition: transform .2s ease-in-out;
    
}

.cpanel tr:hover {
    font-weight: bold;
    opacity: 1;
    transform: scale(1.1);
    font-family: cursive;
}

.cpanel tr:nth-child(odd) {
    background-color: #ffffff85;
}

.isoValue{
   
    border: solid 1px #74525b;
    border-radius: 8px;
    height: 7px;
    
    outline: none;
    transition: background 450ms ease-in;
    -webkit-appearance: none;
}

@import url('https://fonts.googleapis.com/css?family=Lato:100&display=swap');
.btn {
    width: 180px;
    height: 40px;
    cursor: pointer;
    background: transparent;
    border: 1px solid #000000;
    background-color: rgba(255, 255, 255, 0.459);
    outline: none;
    transition: 0.7s ease-in-out;
    margin: 5px;
}

.btn:hover {
    transition: 0.7s ease-in-out;
    background: rgb(119, 59, 85);
}

.btn span {
    color: rgb(0, 0, 0);
    font-size: 17px;
    transition: 0.5s ease-in-out;
    font-weight: 100;
}

.btn span:hover {
    transition: 0.5s ease-in-out;
    color: rgb(255, 255, 255);
}
.blendmode{
    color:#fff ;
    margin-left: 1250px;
    display: none;
}


.typing-demo {
    margin-left: 520px;
    margin-top: 340px ;
    float: inline-start;
    width: 22ch;
    animation: typing 2s steps(22), blink .5s step-end infinite alternate;
    white-space: nowrap;
    overflow: hidden;
    border-right: 3px solid;
    font-family: monospace;
    font-size: 3em;
    color: #fff;
    font-weight: bolder;
  }
  
  @keyframes typing {
    from {
      width: 0
    }
  }
      
  @keyframes blink {
    50% {
      border-color: transparent
    }
  }


